body {
  background: url(./flies.jpg) no-repeat center center fixed;
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
}

h1 {
  font-size: 55px;
  color: black;
  margin-top: 155px;
  background: transparent;
  border: 1px solid black fixed;
  border-radius: 2px;
}

.clock-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  top: 12vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

button {
  font-family: 'Oswald', sans-serif;
  cursor: pointer;
  background-color: transparent;
  color: black;
  border: 1px solid black;
  border-radius: 1px;
  margin: 5px;
}

.buttons-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  top: 38vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}



